<template>
  <div class="nav">
    <div class="w">
      <div class="nav-left">
        <ul>
          <li><a href="#">小米商城</a></li>
          <li class="tree">|</li>
          <li><a href="#">MIUI</a></li>
          <li class="tree">|</li>
          <li><a href="#">IoT</a></li>
          <li class="tree">|</li>
          <li><a href="#">云服务</a></li>
          <li class="tree">|</li>
          <li><a href="#">金融</a></li>
          <li class="tree">|</li>
          <li><a href="#">有品</a></li>
          <li class="tree">|</li>
          <li><a href="#">小爱开放平台</a></li>
          <li class="tree">|</li>
          <li><a href="#">企业团购</a></li>
          <li class="tree">|</li>
          <li><a href="#">资质证照</a></li>
          <li class="tree">|</li>
          <li><a href="#">协议规则</a></li>
          <li class="tree">|</li>
          <li><a href="#">下载app</a></li>
          <li class="tree">|</li>
          <li><a href="#">Select Region</a></li>
        </ul>
      </div>
      <div class="nav-right">
        <ul>
          <li id="nav-right-li1">
            <a href="#">登录</a>
          </li>
          <li class="tree">|</li>
          <li><a href="#">注册</a></li>
          <li class="tree">|</li>
          <li><a href="#">消息通知</a></li>
        </ul>
        <div class="cart" id="cart">
          <a href="#" id="cart-a">
            <span class="sapn">&#xf0179;</span>
            购物车(0)
          </a>
          <div id="cart_content">woshi</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "footers",
    mounted(){
      this.shopget

    },
    methods:{
      shopget(){
        var  cart = document.getElementById('cart')
        var  carta = document.getElementById('cart-a')
        cart.onmouseenter = function () {
          carta.style.color = '#ff6700';
          carta.style.backgroundColor = '#fff';
        }
        cart.onmouseleave = function () {
          carta.style.color = '#b0b0b0'
          carta.style.backgroundColor = '#424242'
        }
      }
    },
  }
</script>



<style scoped lang="scss">
  /*css 初始化 */
  html, body, ul, li, ol, dl, dd, dt, p, h1, h2, h3, h4, h5, h6, form, fieldset, legend, img {
    margin: 0;
    padding: 0;
  }

  fieldset, img, input, button { 			/*fieldset组合表单中的相关元素*/
    border: none;
    padding: 0;
    margin: 0;
    outline-style: none;                /*inuput边框*/
  }

  ul, ol {
    list-style: none;				/*清除列表风格*/
  }

  input {
    padding-top: 0;
    padding-bottom: 0;

  }

  select, input {
    vertical-align: middle;
  }

  select, input, textarea {
    font-size: 12px;
    margin: 0;
  }

  textarea {
    resize: none;
  }

  /*防止多行文本框拖动*/
  img {
    border: 0;
    vertical-align: middle;
  }

  /*  去掉图片低测默认的3像素空白缝隙*/
  table {
    border-collapse: collapse;			/*合并外边线*/
  }


  body {
    font: 12px/150% Arial, Verdana, "\5b8b\4f53";	/*宋体，Unicode，统一码*/
    color: #666;
    background: #fff
  }
  a {
    color: #666;
    text-decoration: none;
  }

  a:hover {
    color: #ff6700;
  }
  .col-red {
    color:  #ff6700 !important;
  }

  /*公共类*/
  .w {
    /*版心 提取 */
    width: 1226px;
    min-width:1226px;
    margin: 0 auto;
  }

  .fl {
    float: left
  }

  .fr {
    float: right
  }

  .al {
    text-align: left
  }

  .ac {
    text-align: center
  }

  .ar {
    text-align: right
  }

  .hide {
    display: none
  }
  .show{
    display: block;
  }
  .nav{
    height: 40px;
    background-color: #333333;
    margin-bottom: 20px;
  }
  .nav-left ul li{
    float: left;
    line-height: 40px;
  }
  .nav-left a, .nav-right a{
    color:#b0b0b0 ;
    font-size: 12px;
  }
  .nav-left a:hover,.nav-right a:hover{
    color:#fff;
  }
  .tree{
    margin: 0 7px;
    color: #424242;
  }
  .nav-right{
    width: 300px;
    float: right;
  }
  .nav-right li{
    float: left;
    line-height: 40px;
  }
  .sapn {
    font: 400 20px/40px zz;

  }

  .cart {
    width: 120px;
    height: 40px;
    float: right;
    text-align: center;
    position: relative;

  }

  .cart a {
    display: inline-block;
    width: 120px;
    height: 40px;
    line-height: 40px;
    background-color: #424242;
  }

  .cart a:hover {

  }

  .cart:hover #cart_content {
    height: 100px;
    transition: all 0.3s ease-in-out;
  }

  #cart_content {
    width: 316px;
    height: 0px;
    line-height: 40px;
    background-color: #fff;
    position: absolute;
    overflow: hidden;
    text-align: center;
    /*display: none;*/
    right: 0;
    z-index: 8888;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.15);
  }

</style>



